.page-component {
  min-height: 80vh;
}

/* Start Footer Styles */
.footer {
  min-height: 15vh;
  background-color:#162e21;
  /* padding-left:200px ;
  padding-right: 200px; */
}

.footer a {
  color: white !important;
}
.footer a:hover {
  text-decoration: none;
}
.footer-links:hover {
  color: #d10014 !important;
  text-decoration: none;
}
.footer-links {
  outline: 0;
  font-size: 1.1rem;
}
.footer-brand {
  border-bottom: solid rgba(255, 255, 255, 0.342) 1px;
}
.copyrights {
  color: rgba(255, 255, 255, 0.342) !important;
}
/* End Footer Styles */

/* Text Styles */
.heading-text {
  font-family: "Playfair Display";
  font-size: 2rem;
}

.small-heading-text {
  font-weight: 400;
  font-size: 1.4rem;
  color: #000000;
}

.blue-link-text {
  font-size: 1.1rem;
  line-height: normal;
  color: #326891;
}

.blue-link-text:hover {
  text-decoration: underline;
  color: #000000;
}

.vertical-line {
  border-left: 1px solid rgba(0, 0, 0, 0.13);
  height: 80vh;
}

